import React, { Component } from 'react';
import './Header.css';
import Logo from '../../assets/Logo-2.svg';
import anime from 'animejs';
import Sidebar from '../Sidebar/Sidebar';

const test = document.body.getElementsByClassName('sidebar');
const header2 = document.body.getElementsByClassName('headerStyle2');

class Header extends Component {
    state = {
        position: window.pageYOffset,
        count: 0,
        sidebar: false
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
        test[0].style.left = "20%";
    }
    

    //WARNING! To be deprecated in React v17. Use componentDidUpdate instead.
    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    handleScroll = () => {
        this.setState(
            { count: window.pageYOffset }
        )
    }

    headerAnime = () => {
        // header2.style.visibility = 'visible';
        anime({
            targets: '.headerStyle2',
            translateY: '210%',
            duration: 500
        })
    }

    goingUp = () => {
        anime({
            targets: '.headerStyle2',
            translateY: '0%',
            duration: 1000
        })
    }

    onClick = () => {
        anime({
            targets: '.sidebar',
            translateX: '-100%',
            duration: 250,
            easing: 'easeInSine'
        })
        var imageArray = [];
        document.body.getElementsByClassName('sidebar')[0].style.visibility = 'visible';
        document.body.getElementsByClassName('landing')[0].style.background = 'rgba(0,0,0,0.6)';
        document.body.getElementsByClassName('landing')[0].style.color = 'rgba(0,0,0,0.6)';
        document.getElementsByClassName('headerStyle1')[0].style.filter = 'brightness(.25)';
        console.log(document.getElementsByClassName('wrapper'));
        imageArray.push(...document.body.getElementsByClassName('images'));
        for (var i = 1; i < imageArray.length; i++) {
            document.getElementsByTagName('img')[i].style.filter = 'brightness(.25)';
        }
        document.body.style.background = 'rgba(0,0,0,0.6)';
        document.body.style.overflow = 'hidden';
        if (window.pageYOffset > 100) {
            console.log(document.getElementById('headWrap'));
            document.getElementsByClassName('headerStyle2')[0].style.filter = 'brightness(.40)';
            document.body.getElementsByClassName('sidebar')[0].style.visibility = 'visible';
        }
    }   

    secondClick = () => {
        header2[0].style.width = '80%';
    }
    render() {
        return (
            <div>
                <div className={"headerStyle1"}>
                    <p></p>
                    <a href={'/'} id={'main'}>FromADistance</a>
                    <p></p>
                    <button id={'topics'} onClick={this.onClick}>Topics</button>
                </div>
                {window.pageYOffset < 100 ? this.goingUp() : this.headerAnime()}
                    <div className={"headerStyle2"} style={{ width: document.body.style.width }}>
                        <p></p>
                        <a href={'/'}><div><img id={'logo'} src={Logo} alt={'From A Distance Logo'}></img></div></a>
                        <p></p>
                        <button id={'topics'} onClick={this.onClick}>Topics</button>
                    </div>
                 {/* }  */}
                <Sidebar />
            </div>
        );
    }
}

export default Header;